import React, { useEffect, useState } from "react";
import PropTypes from "prop-types";
import { Card, Button, Row, Col, message, Spin } from "antd";
import {
  ClockCircleOutlined,
  TeamOutlined,
  TrophyOutlined,
  SyncOutlined,
} from "@ant-design/icons";
import { useNavigate } from "react-router-dom";
import { getUserData } from "../services/userService";
import StudyStats from "../components/features/dashboard/StudyStats";
import RecentActivity from "../components/features/dashboard/RecentActivity";
import ProfileModal from "../components/features/dashboard/ProfileModal";
import "./Dashboard.css";

/**
 * Dashboard component that displays user's study statistics, recent activity,
 * and quick action cards for various features.
 */
const Dashboard = () => {
  const navigate = useNavigate();
  const [userData, setUserData] = useState(null);
  const [isProfileModalVisible, setIsProfileModalVisible] = useState(false);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    fetchUserData();
  }, []);

  const fetchUserData = async () => {
    try {
      setIsLoading(true);
      setError(null);
      const data = await getUserData();
      setUserData(data);
    } catch (error) {
      setError(error.message || "Failed to fetch user data");
      message.error("Failed to fetch user data. Please try again later.");
    } finally {
      setIsLoading(false);
    }
  };

  const handleProfileUpdate = async (updatedData) => {
    try {
      setUserData({ ...userData, ...updatedData });
      setIsProfileModalVisible(false);
      message.success("Profile updated successfully");
    } catch (error) {
      message.error("Failed to update profile");
    }
  };

  const handleRefresh = () => {
    fetchUserData();
  };

  // Action cards configuration
  const actionCards = [
    {
      title: "Focus Time",
      icon: (
        <ClockCircleOutlined style={{ fontSize: "24px", color: "#1890ff" }} />
      ),
      description: "2h 30m / 4h Goal",
      buttonText: "Start Timer",
      onClick: () => navigate("/timer"),
    },
    {
      title: "Study Streak",
      icon: <TrophyOutlined style={{ fontSize: "24px", color: "#52c41a" }} />,
      description: "7 Days",
      buttonText: "View Stats",
      onClick: () => navigate("/stats"),
    },
    {
      title: "Study Partners",
      icon: <TeamOutlined style={{ fontSize: "24px", color: "#722ed1" }} />,
      description: "3 New Matches",
      buttonText: "Join Session",
      onClick: () => navigate("/study-group"),
    },
  ];

  if (isLoading) {
    return (
      <div className="dashboard-loading">
        <Spin size="large" tip="Loading your dashboard..." />
      </div>
    );
  }

  if (error) {
    return (
      <div className="dashboard-error">
        <h2>Something went wrong</h2>
        <p>{error}</p>
        <Button type="primary" onClick={handleRefresh} icon={<SyncOutlined />}>
          Try Again
        </Button>
      </div>
    );
  }

  if (!userData) {
    return null;
  }

  return (
    <div className="dashboard-container">
      <div className="dashboard-header">
        <div className="header-content">
          <h1>Welcome back, {userData.name.split(" ")[0]}!</h1>
          <Button
            icon={<SyncOutlined />}
            onClick={handleRefresh}
            type="text"
            title="Refresh dashboard"
          />
        </div>
      </div>

      <Row gutter={[24, 24]} className="action-cards">
        {actionCards.map((card, index) => (
          <Col xs={24} sm={8} key={index}>
            <Card className="action-card">
              <div className="action-icon">{card.icon}</div>
              <h3>{card.title}</h3>
              <p>{card.description}</p>
              <Button type="primary" onClick={card.onClick}>
                {card.buttonText}
              </Button>
            </Card>
          </Col>
        ))}
      </Row>

      <div className="profile-section">
        <div className="profile-header">
          <h2>Your Profile</h2>
          <button
            className="edit-profile-btn"
            onClick={() => setIsProfileModalVisible(true)}
          >
            Edit Profile
          </button>
        </div>
        <div className="profile-details">
          <div className="detail-item">
            <span className="label">Education Level:</span>
            <span className="value">{userData.education}</span>
          </div>
          <div className="detail-item">
            <span className="label">Field of Study:</span>
            <span className="value">{userData.major}</span>
          </div>
          <div className="detail-item">
            <span className="label">Interests:</span>
            <span className="value">{userData.interests}</span>
          </div>
          <div className="detail-item">
            <span className="label">Hobbies:</span>
            <span className="value">{userData.hobbies}</span>
          </div>
          <div className="detail-item">
            <span className="label">Study Preference:</span>
            <span className="value">{userData.studyPreference}</span>
          </div>
        </div>
      </div>

      <div className="dashboard-grid">
        <div className="dashboard-main">
          <StudyStats userData={userData} onRefresh={handleRefresh} />
        </div>
        <div className="dashboard-side">
          <RecentActivity userData={userData} onRefresh={handleRefresh} />
        </div>
      </div>

      <ProfileModal
        visible={isProfileModalVisible}
        onCancel={() => setIsProfileModalVisible(false)}
        onFinish={handleProfileUpdate}
        initialValues={userData}
      />
    </div>
  );
};

// PropTypes for child components
StudyStats.propTypes = {
  userData: PropTypes.shape({
    id: PropTypes.string,
    name: PropTypes.string,
    stats: PropTypes.object,
  }).isRequired,
  onRefresh: PropTypes.func.isRequired,
};

RecentActivity.propTypes = {
  userData: PropTypes.shape({
    id: PropTypes.string,
    name: PropTypes.string,
    activities: PropTypes.array,
  }).isRequired,
  onRefresh: PropTypes.func.isRequired,
};

export default Dashboard;
